'use client';

import React from 'react';
import Image from 'next/image';
import { useTheme } from 'next-themes';

interface CertificateCardProps {
  title: string;
  subtitle: string;
  status: 'locked' | 'completed';
  image: string;
  lockedImage: string;
  descriptionLocked: string;
  descriptionUnlocked: string;
  shareUrl: string;
}

const CertificateCard: React.FC<CertificateCardProps> = ({
  title,
  subtitle,
  status,
  image,
  lockedImage,
  descriptionLocked,
  descriptionUnlocked,
  shareUrl,
}) => {
  const { theme } = useTheme();

  const isCompleted = status === 'completed';

  const lockIcon = `/assets/${theme === 'dark' ? 'dark' : 'light'}/${isCompleted ? 'unlocked.svg' : 'lock.svg'}`;

  const socialLinks = [
    {
      name: 'WhatsApp',
      url: `https://api.whatsapp.com/send?text=Mira mi certificado: ${shareUrl}`,
      icon: `/assets/${theme === 'dark' ? 'dark' : 'light'}/whatsapp.svg`,
    },
    {
      name: 'Facebook',
      url: `https://www.facebook.com/sharer/sharer.php?u=${shareUrl}`,
      icon: `/assets/${theme === 'dark' ? 'dark' : 'light'}/facebook.svg`,
    },
    {
      name: 'LinkedIn',
      url: `https://www.linkedin.com/sharing/share-offsite/?url=${shareUrl}`,
      icon: `/assets/${theme === 'dark' ? 'dark' : 'light'}/linkedin.svg`,
    },
  ];

  return (
    <div
      className={`relative p-6 rounded-xl border shadow-md flex flex-col items-center text-center min-h-[500px] transition-opacity ${
        isCompleted ? 'opacity-100' : 'opacity-50'
      } bg-[var(--background-2)]`}
    >
      {/* Estado (lock/unlock) */}
      <div className="absolute top-6 right-6">
        <Image src={lockIcon} alt="Estado" width={24} height={24} />
      </div>

      {/* TÃ­tulo */}
      <h2 className="text-xl font-bold text-[var(--color-primary-900)]">{title}</h2>
      <p className="text-sm text-[var(--color-neutral-600)] mb-4">{subtitle}</p>

      {/* Imagen (certificado o personaje) */}
      <div className="relative w-full h-40 my-4 flex justify-center">
        <Image
          src={isCompleted ? image : lockedImage}
          alt={title}
          width={220}
          height={160}
          className="object-contain rounded-md"
        />
      </div>

      {/* DescripciÃ³n */}
      <p className="p14 px-2 text-[var(--color-neutral-600)]">
        {isCompleted ? descriptionUnlocked : descriptionLocked}
      </p>

      {/* BotÃ³n de descarga */}
      {isCompleted && (
        <button className="mt-5 px-6 py-2 border-2 border-[var(--primary-color)] text-[var(--primary-color)] rounded-lg font-bold text-sm hover:bg-[var(--primary-color)] hover:text-white transition-colors">
          Descargar Certificado
        </button>
      )}

      {/* Redes sociales */}
      {isCompleted && (
        <div className="mt-6 w-full">
          <p className="text-sm font-semibold text-[var(--color-neutral-600)] mb-2">CompÃ¡rtelo</p>
          <div className="flex justify-center gap-5">
            {socialLinks.map(({ name, url, icon }) => (
              <a
                key={name}
                href={url}
                target="_blank"
                rel="noopener noreferrer"
                className="transition-transform hover:scale-110"
              >
                <Image src={icon} alt={name} width={24} height={24} />
              </a>
            ))}
          </div>
        </div>
      )}
    </div>
  );
};

export default CertificateCard;
